.footer {
    background-color: var(--grey);
    padding: 4rem 0 0;
}

.footer__top {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2.5rem;
    padding-bottom: 4.1rem;
}

.footer__column {
    flex: 1 1 20%;
    display: flex;
    flex-direction: column;
}

.footer__logo {
    width: 14rem;
    height: 5rem;

}

.footer__logo img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.footer__rek {
    font-size: 1.4rem;
    margin-top: auto;
}

.footer__rek p:not(:last-child) {
    margin-bottom: .2rem;
}

.footer__title {
    font-size: 1.8rem;
    font-family: 'FuturaPT', sans-serif;
    font-weight: 500;
    margin-bottom: 1rem;
}

.footer__links,
.footer__contacts {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: start;
}

.footer__link {
    font-size: 1.6rem;
    transition: color .3s ease-in-out;
}

@media (hover:hover) {
    a.footer__link:hover {
        color: var(--blue);
    }
}

@media (hover:none) {
    a.footer__link:active {
        color: var(--blue);
    }
}

.footer .header__socials__icons {
    margin-top: .5rem;
}

.footer__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2.1rem 0 2.8rem;
    border-top: 1px solid rgba(73, 80, 84, 0.25);
}

.footer__bottom-text {
    font-size: 1.6rem;
    color: rgba(73, 80, 84, 0.5);
}

.footer__bottom-dev {
    font-size: 1.6rem;
    font-weight: 500;
    font-family: 'FuturaPT', sans-serif;
    color: rgba(73, 80, 84, 0.5);
}

.footer__bottom-dev a, .footer__bottom-policy {
    color: var(--blue);
    transition: color .3s ease-in-out;
    text-decoration: underline;
    text-underline-offset: .2rem;
}

@media (hover:hover) {
    .footer__bottom-dev a:hover, .footer__bottom-policy:hover {
        color: var(--default);
    }
}

@media (hover:none) {
    .footer__bottom-dev a:active, .footer__bottom-policy:active {
        color: var(--default);
    }
}
